<template>
  <div>
    <v-row>
      <v-col cols="3">
        <v-btn v-if="editable" class="mb-4" block @click="(subscriptionDialog = true), (subscriptionItem = {enable: true})">
          <v-icon>mdi-shape-square-plus</v-icon>&nbsp;New Subscription
        </v-btn>
        <v-btn :key="index" v-for="(s, index) of subscriptions" :style="`border-color: ${s.color}`"
          :class="`subscription-item border-left`">
          <span class="truncate">{{ s.topic }}</span>
          <span class="white--text d-flex">
            <v-switch @change="onSave" right v-model="s.enable" class="mqtt-switch-btn"></v-switch>
            <a v-if="editable" @click="editSubscription(index)" @click.stop="stopTheEvent" style="margin: auto"><v-icon small color="blue"> mdi-pencil-circle </v-icon></a>&nbsp;
            <a v-if="editable" @click="removeSubscription(index)" @click.stop="stopTheEvent" style="margin: auto"  ><v-icon small color="red"> mdi-close-circle </v-icon></a>
          </span>
        </v-btn>
      </v-col>
      <v-col cols="9">
        <v-card elevation="2">
          <v-card-text>
            <v-row>
              <v-spacer></v-spacer>
              <div class="text-right mb-5">
                <v-menu offset-y>
                  <template v-slot:activator="{ on, attrs }">
                    <v-btn x-small color="#009688" class="white--text"  dark v-bind="attrs" v-on="on" >Download<v-icon x-small right dark>mdi-download</v-icon></v-btn>
                  </template>
                  <v-list>
                    <v-list-item link>
                      <v-list-item-title @click="clickDownload(filteredMessages, 'json')">Json</v-list-item-title>
                    </v-list-item>
                    <v-list-item link>
                      <v-list-item-title @click="clickDownload(filteredMessages, 'csv')">CSV</v-list-item-title>
                    </v-list-item>
                  </v-list>
                </v-menu>&nbsp;
                <v-btn text small color="warning" @click="messages = []">Clear</v-btn>
                <v-btn @click="pauseMessage" icon small class="white--text">
                  <v-icon small dark >
                    mdi-{{pause ? 'play': 'pause'}}
                  </v-icon>
                </v-btn>
                <v-btn-toggle v-if="editable" v-model="toggle" mandatory>
                  <v-btn :value="'all'" small>All</v-btn>
                  <v-btn :value="'receive'" small>Received</v-btn>
                  <v-btn :value="'publish'" small>Published</v-btn>
                </v-btn-toggle>
              </div>
            </v-row>
            <div id="message-container">
              <div v-for="(m, index) in filteredMessages" :key="index" class="message">
                <v-chip v-if="m.type === 'publish'" :color="m.color" :class="`${m.type}-message`">
                  Topic: {{ m.topic }} <br />
                  Message: {{ m.text }} <br />
                  Time: {{m.time | timezone}}
                </v-chip>
                <v-chip v-if="m.type === 'receive'" :class="`${m.type}-message border-left`" :style="`border-color: ${m.color}`" >
                  Topic: {{ m.topic }} <br />
                  Message: {{ m.text }} <br />
                  Time: {{m.time | timezone}}
                </v-chip>
              </div>
            </div>
          </v-card-text>
          <v-card-actions v-if="editable">
            <v-form style="width: 100%" v-model="valid" ref="form" lazy-validation>
              <v-text-field v-model="topic" label="Topic" :rules="[required]"></v-text-field>
              <v-text-field @keyup.enter.native="sendMessage" v-model="message" label="Message" :rules="[required]">
                <v-icon slot="append" color="primary" @click="sendMessage"> mdi-send </v-icon>
              </v-text-field>
            </v-form>
          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>
    <v-dialog v-model="subscriptionDialog" max-width="400px">
      <subscription-form :onCancel="onCloseSubscription" :onSave="onSaveSubscription" :item="subscriptionItem" ></subscription-form>
    </v-dialog>

    <v-dialog v-model="confirmDialog" persistent max-width="290" >
      <v-card>
        <v-card-title class="headline">
          Confirm?
        </v-card-title>
        <v-card-text>Are you sure?</v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="green darken-1" text @click="confirmDialog = false" >
            No
          </v-btn>
          <v-btn color="red darken-1" text @click="confirm">
            Yes
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
import SubscriptionForm from './SubscriptionForm';

import axios from '../../axios';
import _cloneDeep from 'lodash/cloneDeep'
import { mqttWildcard, copyToClipboard, exportCSV, exportJSON } from '../../utils/utils';

export default {
  props: {
    editable: {
      type: Boolean,
      default: true
    },
    subscriptions: {
      type: Array,
      default: []
    }
  },
  components: {
    SubscriptionForm
  },
  data: () => ({
    subscriptionDialog: false,
    required: value => !!value || 'Required.',
    message: '',
    topic: '',
    pause: false,
    toggle: 'all',
    selectedIndex: -1,
    valid: false,
    confirmDialog: false,
    messages: [],
    subscriptionItem: {},
    editedIndex: -1,
  }),
  mounted() {
    const { hostname } = window.location;
    this.socket = new WebSocket(`ws://${hostname}:1414`, 'mqtt');
    this.socket.addEventListener('message', ({ data }) => {
      const message = JSON.parse(data);
      if (!this.pause) {
        this.messages.push({ ...message, type: 'receive' });
      }
    });
  },
  computed: {
    mqttConfig() {
      return this.$store.state.mqttConfig;
    },
    filteredMessages() {
      let messages = [...this.messages];
      let subscriptions = [];
      const subscriptionColor = {};
      if(this.subscriptions){
        subscriptions = this.subscriptions;
        for (const s of subscriptions) {
          subscriptionColor[s.topic] = s.color;
        }
      }
      messages = messages.filter(m => {
        let result = false;
        for (const subscription of subscriptions) {
          const isMatch = mqttWildcard(m.topic, subscription.topic);
          if (isMatch && subscription.enable) {
            result = true;
            m.color = subscriptionColor[subscription.topic];
            break;
          }
        }
        if (this.toggle !== 'all') {
          result &= m.type === this.toggle;
        }
        return result;
      });
      return messages;
    },
  },
  updated() {
    const container = this.$el.querySelector('#message-container');
    container.scrollTop = container.scrollHeight;
  },
  methods: {
    pauseMessage() {
      this.pause = !this.pause;
    },
    onSave() {
      axios.put('mqtt/config', this.mqttConfig).then(r => {
        this.$toasted.show('Success');
        this.$store.commit('changeMqttConfig', this.mqttConfig);
      });
    },
    clickDownload(array, type){
      if(type === "csv"){
          return exportCSV('mqtt-messages', array);
      }
      if(type === "json"){
          return exportJSON('mqtt-messages', JSON.stringify(array));
      }
    },
    confirm() {
      delete this.subscriptions.splice(this.selectedIndex, 1);
      this.confirmDialog = false;
      this.onSave();
    },
    removeSubscription(index) {
      this.confirmDialog = true;
      this.selectedIndex = index;

    },
    editSubscription(index) {
      this.subscriptionItem = _cloneDeep(this.subscriptions[index]);
      this.subscriptionDialog = true;
      this.editedIndex = index;
    },
    async sendMessage() {
      this.$refs.form.validate();
      const isValid = this.$refs.form.validate();
      if (!isValid) {
        return;
      }
      const data = { topic: this.topic, message: this.message };
      this.messages.push({ type: 'publish', text: this.message, topic: this.topic, time: new Date() });
      await axios.post('mqtt/send-message', data);
    },
    onCloseSubscription() {
      this.subscriptionDialog = false;
      this.subscriptionItem = {};
      this.editedIndex = -1;
    },
    onSaveSubscription(data) {
      if (this.editedIndex > -1) {
        this.subscriptions[this.editedIndex] = data;
      } else if (this.subscriptions) {
        this.subscriptions.push(data);
      } else {
        this.subscriptions = [data];
      }
      this.onSave();
      this.onCloseSubscription();
    },
    stopTheEvent(event) {
      event.stopPropagation();
    },
  },
};
</script>

