<template>
  <v-card>
    <v-card-title>
      <span class="headline">New Subscription</span>
    </v-card-title>
    <v-card-text>
      <v-container>
        <v-form v-model="valid" ref="form" lazy-validation>
          <v-row>
            <v-col cols="12">
              <v-text-field v-model="formData.topic" label="Topic" required :rules="[rules.required]"></v-text-field>
            </v-col>
            <v-col cols="12">
              <v-color-picker
                dot-size="25"
                v-model="formData.color"
                :mode="'hexa'"
                swatches-max-height="200"
              ></v-color-picker>
            </v-col>
          </v-row>
        </v-form>
      </v-container>
    </v-card-text>

    <v-card-actions>
      <v-spacer></v-spacer>
      <v-btn color="darken-1" text @click="onCancel"> Cancel </v-btn>
      <v-btn color="blue darken-1" :disabled="!valid" text @click="onSave(formData)"> Save </v-btn>
    </v-card-actions>
  </v-card>
