<script>

  export default {
    props: ['onSave', 'onCancel', 'item'],
    data: () => ({
      rules: { required: (value) => !!value || "Required." },
      valid: false,
    }),
    mounted() {
      this.$refs.form.validate()
    },
    methods: {
     
    },
    computed: {
      formData () {
        this.item.color = this.item.color ? this.item.color : '#FF0000FF';
        return this.item;
      },
    },
  };
</script>
