<template>
  <v-app>
    <mb-right-sidebar></mb-right-sidebar>
    <v-main>
      <div class="table-styling">
        <v-tabs v-model="tab" right dark>
          <v-breadcrumbs :items="dirs" class="breadcrumbs">
            <template v-slot:item="{ item }">
              <v-breadcrumbs-item class="breadcrumbs-item-text" :to="item.link" exact> {{ item.text }} </v-breadcrumbs-item>
            </template>
          </v-breadcrumbs>
          <v-tab :key="0" style="margin-left: auto"> Data </v-tab>
          <v-tab :key="1"> Debug Messages </v-tab>
        </v-tabs>
        <v-tabs-items v-model="tab">
          <v-tab-item :key="0">
            <v-data-table
              v-model="selected"
              :headers="deviceHeaders"
              :items="dataTable"
              class="elevation-1 text-left"
              item-key="uuid"
              show-select
              :item-class="backgroundColor"
              :footer-props="{
                showFirstLastPage: true,
                firstIcon: 'mdi-chevron-double-left',
                lastIcon: 'mdi-chevron-double-right',
                prevIcon: 'mdi-chevron-left',
                nextIcon: 'mdi-chevron-right',
              }"
              :search="search"
              sort-by="uuid"
              group-by="category"
              show-group-by
            >
              <template v-slot:top>
                <v-toolbar flat>
                  <v-toolbar-title>Devices View</v-toolbar-title>
                  <v-divider class="mx-4" inset vertical></v-divider>
                  <div class="text-left pa-2">
                    <v-btn @click="onClickRefresh" x-small color="primary" class="white--text"> Refresh<v-icon x-small right dark> mdi-refresh </v-icon> </v-btn >&nbsp;
                    <v-btn @click="clickDeleteMulti" :disabled="selected.length === 0" x-small color="error" class="white--text" >Delete<v-icon x-small right dark> mdi-delete </v-icon> </v-btn>&nbsp;
                  </div>
                  <v-spacer></v-spacer>
                  <v-text-field v-model="search" append-icon="mdi-magnify" label="Search" single-line hide-details ></v-text-field>
                </v-toolbar>
              </template>
              <template v-slot:group.header="{ group, headers, toggle, isOpen, items }">
                <td :colspan="headers.length">
                  <div class="row" style="align-items: center;">
                      <v-btn @click="toggle" x-small icon :ref="group">
                        <v-icon>{{ isOpen ? 'mdi-chevron-double-up' : 'mdi-chevron-double-down' }}</v-icon>
                      </v-btn>&nbsp;&nbsp;&nbsp;&nbsp;
                      <v-checkbox v-bind:value="selected.filter(d=>d.network_uuid === items[0].network_uuid).length == items.length" @change="check($event, items)"></v-checkbox>
                      <router-link :to="`/modbus/networks/${items[0].network_uuid}/devices`" class="font-weight-bold text-style"> {{group}} </router-link>
                    </div>
                </td>
              </template>
              <template v-slot:item.enable="{ item }" >
                <v-checkbox v-model="item.enable" @click="updateDevice(item)"></v-checkbox>
              </template>
              <template v-slot:item.supports_multiple_rw="{ item }" >
                <v-checkbox v-model="item.supports_multiple_rw" @click="updateDevice(item)"></v-checkbox>
              </template>
              <template v-slot:item.address="{ item }">
                <v-edit-dialog :return-value.sync="item.address" @save="updateDevice(item)" @cancel="cancelEdit" @open="openEdit(item)" @close="closeEdit" large >
                  <b>{{ item.address }}</b>
                  <template v-slot:input>
                    <v-text-field v-model="item.address" single-line type='number' min="0" @input="numberChange()" :rules="[() => !isExistingvalue(item.address, 'address', item) || 'Address Existed']"></v-text-field>
                  </template>
                </v-edit-dialog>
              </template>
              <template v-slot:item.tcp_ip="{ item }">
                <v-edit-dialog :return-value.sync="item.tcp_ip" @save="updateDevice(item)" @cancel="cancelEdit" @open="openEdit(item)" @close="closeEdit" large >
                  <b>{{ item.tcp_ip }}</b>
                  <template v-slot:input>
                    <v-form v-model="valid" ref="form" lazy-validation>
                      <v-text-field v-model="item.tcp_ip" single-line :rules="[() => !isExistingvalue(item.tcp_ip, 'tcp_ip', item) || 'IP Existed']"></v-text-field>
                    </v-form>
                  </template>
                </v-edit-dialog>
              </template>
              <template v-slot:item.tcp_port="{ item }">
                <v-edit-dialog :return-value.sync="item.tcp_port" @save="updateDevice(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
                  <b>{{ item.tcp_port }}</b>
                  <template v-slot:input>
                    <v-text-field v-model="item.tcp_port" single-line type='number' min="0" @input="numberChange()"></v-text-field>
                  </template>
                </v-edit-dialog>
              </template>
              <template v-slot:item.ping_point="{ item }">
                <v-edit-dialog :return-value.sync="item.ping_point" @save="updateDevice(item)" @cancel="cancelEdit" @open="openEdit" @close="closeEdit" large >
                  <b>{{ item.ping_point }}</b>
                  <template v-slot:input>
                    <v-text-field v-model="item.ping_point" single-line></v-text-field>
                  </template>
                </v-edit-dialog>
              </template>

              <template v-slot:item.actions="{ item }">
                <v-icon small @click="editItem(item)" color="primary">mdi-pencil</v-icon>&nbsp;
                <v-icon small @click="clickDelete(item)" color="error">mdi-delete</v-icon>&nbsp;
              </template>

              <!-- header -->
              <template v-slot:header.enable="{ item }" >
                <div class="d-flex">
                  <span>{{ toUpperTextKey('enable') }}</span>&nbsp;&nbsp;
                  <v-icon x-small @click="openMassEditDialog('enable')" color="primary">mdi-pencil</v-icon>
                </div>
              </template>
              <template v-slot:header.supports_multiple_rw="{ item }" >
                <div class="d-flex">
                  <span>{{ toUpperTextKey('supports_multiple_rw') }}</span>&nbsp;&nbsp;
                  <v-icon x-small @click="openMassEditDialog('supports_multiple_rw')" color="primary">mdi-pencil</v-icon>
                </div>
              </template>
              <template v-slot:header.tcp_port="{ item }" >
                <div class="d-flex">
                  <span>{{ toUpperTextKey('port') }}</span>&nbsp;&nbsp;
                  <v-icon x-small @click="openMassEditDialog('tcp_port')" color="primary">mdi-pencil</v-icon>
                </div>
              </template>
              <template v-slot:header.ping_point="{ item }" >
                <div class="d-flex">
                  <span>{{ toUpperTextKey('ping_point') }}</span>&nbsp;&nbsp;
                  <v-icon x-small @click="openMassEditDialog('ping_point')" color="primary">mdi-pencil</v-icon>
                </div>
              </template>

              <template v-slot:no-data> No data </template>
            </v-data-table>
          </v-tab-item>
          <v-tab-item :key="2">
            <mqtt-console :editable="false" :subscriptions="debugSubscriptions"></mqtt-console>
          </v-tab-item>
        </v-tabs-items>
      </div>
    </v-main>

    <!--Edit dialog-->
    <v-dialog v-model="dialog" max-width="500px">
      <v-card>
        <v-card-title>
          <span class="headline">Edit Device</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-form v-model="valid" ref="form" lazy-validation>
              <v-row>
                <v-col cols="12" sm="12" md="12">
                  <v-text-field v-model="network.name" disabled class="centered-input"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.name" label="device name" required :rules="[() => !isExistingvalue(editedItem.name, 'name', editedItem) || 'Name Existed', rules.required]" ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-checkbox v-model="editedItem.enable" label="enable"></v-checkbox>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-checkbox v-model="editedItem.supports_multiple_rw" label="supports multiple r/w"></v-checkbox>
                </v-col>
                <v-col cols="12" sm="6" md="4" v-if="network.type == 'TCP'">
                  <v-text-field v-model="editedItem.tcp_ip" label="IP" :rules="[() => !isExistingvalue(editedItem.tcp_ip, 'tcp_ip', editedItem) || 'IP Existed']"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4" v-if="network.type == 'TCP'">
                  <v-text-field v-model="editedItem.tcp_port" label="Port" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-checkbox v-model="editedItem.zero_mode" label="zero mode"></v-checkbox>
                </v-col>
                <v-col cols="12" sm="6" md="8">
                  <v-text-field v-model="editedItem.ping_point" label="ping point (Func:Addr:Len 1:1:1)"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="4">
                  <v-text-field v-model="editedItem.address" label="address" type="number" :rules="[() => !isExistingvalue(editedItem.address, 'address', editedItem) || 'Address Existed']" ></v-text-field>
                </v-col>
              </v-row>
            </v-form>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="close">Cancel</v-btn>
          <v-btn color="blue darken-1" text @click="updateDevice(editedItem)" :disabled="!valid">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- Delete dialog -->
    <confirm-dialog :is-open="dialogDelete" :on-close="closeDelete" :on-confirm="confirm" dialog-text="Do you want to delete ?"></confirm-dialog>

    <!-- Mass Edit dialog-->
    <v-dialog v-model="dialogMassEdit" width="300px">
        <v-card>
          <v-card-title>Mass Edit</v-card-title>
          <v-card-text>
            <v-checkbox v-if="typeof editAll[editField] === 'boolean'" v-model="editAll[editField]" :label="toUpperTextKey(editField)"/>
            <v-text-field v-if="typeof editAll[editField] === 'number'" min="0" v-model.number="editAll[editField]"  :label="toUpperTextKey(editField)" type="number" @input="numberChange()"></v-text-field>
            <v-text-field v-if="typeof editAll[editField] === 'string'" v-model="editAll[editField]" :label="toUpperTextKey(editField)" ></v-text-field>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="darken-1" text @click="dialogMassEdit = false">Close</v-btn>
            <v-btn color="primary darken-1" text @click="saveMassEdit(editAll[editField], editField)">Save</v-btn>
          </v-card-actions>
        </v-card>
    </v-dialog>
  </v-app>
</template>

<script>
import { toUpperTextKey } from '../../utils/utils';
import axios from '../../axios';
import _cloneDeep from 'lodash/cloneDeep';
import MqttConsole from '../Mqtt/MqttConsole';
import MbRightSidebar from './MbRightSidebar';
import ConfirmDialog from '../Common/ConfirmDialog';

export default {
  components: {
    MqttConsole,
    MbRightSidebar,
    ConfirmDialog
  },
  data: () => ({
    topic: 'rubix/points/debug',
    tab: 0,
    action: '',
    valid: false,
    dialog: false,
    dialogDelete: false,
    selected: [],
    deviceHeaders: [
      { text: 'Actions', value: 'actions', sortable: false , groupable: false },
      { text: 'Name', value: 'name', align: 'start' , groupable: false },
      { text: 'Fault', value: 'fault', sortable: false , groupable: false },
      { text: 'Type', value: 'type', sortable: false , groupable: false },
      { text: 'Enable', value: 'enable', sortable: false , groupable: false },
      { text: 'Supports multiple r/w', value: 'supports_multiple_rw', sortable: false , groupable: false },
      { text: 'Address', value: 'address', sortable: false , groupable: false },
      { text: 'IP', value: 'tcp_ip', sortable: false , groupable: false },
      { text: 'Port', value: 'tcp_port', sortable: false , groupable: false },
      { text: 'Ping Point', value: 'ping_point', sortable: false , groupable: false },
      // { text: 'Point Interval MS Between Points', value: 'point_interval_ms_between_points', sortable: false , groupable: false },
      // { text: 'Timeout', value: 'timeout', sortable: false , groupable: false },
      { text: 'UUID', value: 'uuid', align: 'start', sortable: false , groupable: false },
    ],
    currentItem: {},
    editedItem: { },
    rules: {
      required: value => !!value || 'Required.',
    },
    search: '',
    dialogMassEdit: false,
    editAll: {
      enable: false,
      supports_multiple_rw: false,
      tcp_port: 0,
      ping_point: "",
      history_enable: false,
      // timeout: 0,
      // point_interval_ms_between_points: 0,
    },
    editField: "",
    intervalId: null,
    devicesOfNW: [],
    dataTable: [],
    dirs: [{text: "NETWORKS", link: '/modbus/networks'}, { text: 'DEVICES VIEW', disabled: true }],
    networks: [],
    network: {},
  }),
  computed: {
    refreshTime() {
      const { setting } = this.$store.state;
      return setting.refreshTime;
    },
    enableAutoRefresh() {
      const { setting } = this.$store.state;
      return setting.enableAutoRefresh;
    },
    toastAutoRefresh() {
      const { setting } = this.$store.state;
      return setting.toastAutoRefresh;
    },
    debugSubscriptions () {
      const { debugSubscriptions } = this.$store.state.mqttConfig;
      return debugSubscriptions.modbus;
    },
  },
  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
    '$route.params.id':function(val, oldVal){
      if(val != oldVal){
        this.getAllDevices();
      }
    }
  },
  beforeRouteLeave (to, from, next) {
    clearInterval(this.intervalId);
    axios.post(`mqtt/unsubscribe`, {topic: this.topic}).finally(() => {
        next();
      });
  },
  mounted() {
    this.getAllDevices();

    //set auto refresh
    if(this.refreshTime &&  this.enableAutoRefresh) {
      this.intervalId = setInterval(()=>{
        this.getAllDevices();
        if(this.toastAutoRefresh){
          this.$toasted.show(`Automatically refresh every ${this.refreshTime}ms`, { type: 'info' });
        }
      }, this.refreshTime);
    }
  },
  methods: {
    getAllDevices() {
      axios.get(`point-server/network/?with_children=true`).then(data => {
        this.networks = data;
        this.getDataTable(data);
      });
    },
    getDataTable(networks){
      this.dataTable = [];
      networks.forEach(network=>{
        if(network.devices.length > 0){
          const devicesPreNW = network.devices.map(device=>{
            return { ...device, category: network.name };
          });
          this.dataTable = this.dataTable.concat(devicesPreNW);
        }
      });
    },
    editItem(item) {
      this.editedIndex = this.dataTable.findIndex(d=>d.uuid == item.uuid);
      this.devicesOfNW = this.dataTable.filter(device=>device.network_uuid === item.network_uuid);
      this.editedItem = Object.assign({}, item);
      this.network = this.networks.find(n => n.uuid === item.network_uuid);
      this.dialog = true;
    },
    async clickDelete(item) {
      this.action = 'deleteSingle';
      this.dialogDelete = true;
      this.currentItem = item;
      this.editedItem = Object.assign({}, item);
    },
    async clickDeleteMulti() {
      this.dialogDelete = true;
      this.action = 'deleteSelected';
    },
    async deleteSingle() {
      this.editedIndex = this.dataTable.indexOf(this.currentItem);
      this.dataTable.splice(this.editedIndex, 1);
      await axios.delete(`point-server/device/${this.currentItem.uuid}`);
      this.closeDelete();
    },
    async deleteSelected() {
      const promises = [];
      for (const item of this.selected) {
        this.currentItem = item;
        promises.push(this.deleteSingle());
      }
      await Promise.all(promises);
      this.selected = [];
    },
    async confirm() {
      await this[this.action]();
      this.dialog = false;
      this.action = '';
    },
    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = {};
        this.editedIndex = -1;
      });
    },
    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = {};
        this.editedIndex = -1;
      });
    },
    isExistingvalue(value, fieldName, item) {
      let devices = _cloneDeep(this.devicesOfNW);
      const index = devices.findIndex(d=>d.uuid == item.uuid);
      devices.splice(index, 1);
      if (fieldName === 'address' || fieldName === 'tcp_port') {
        value = Number(value);
      }
      return devices.find(d => d[fieldName] == value) != null;
    },
    updateDevice(data) {
      axios.put(`point-server/device/${data.uuid}`, data).then(response => {
        const index = this.dataTable.findIndex(d => d.uuid == response.uuid);
        Object.assign(this.dataTable[index], response);
        this.close();
        this.$toasted.show('update success');
      });
    },
    backgroundColor(item) {
      let classColor = '';
      if (item.fault) {
        classColor = 'background-fault-true';
      } else {
        classColor = !item.enable ? 'background-enable-false' : '';
      }
      return classColor;
    },
    saveMassEdit (value, field) {
      for (const item of this.selected) {
        item[field] = value;
        this.updateDevice(item);
      }
      this.refresh();
    },
    async openMassEditDialog(fieldName){
      if(this.selected.length < 1){
        return this.$toasted.show("Please select item first!", {type: 'error'});
      }
      this.dialogMassEdit = true;
      this.editField = fieldName;
    },
    refresh(){
      this.editAll = {
        enable: false,
        supports_multiple_rw: false,
        tcp_port: 0,
        ping_point: "",
        history_enable: false,
        // timeout: 0,
        // point_interval_ms_between_points: 0,
      }
      this.dialogMassEdit = false;
    },
    toUpperTextKey(key) {
      if(!key) return;
      return toUpperTextKey(key);
    },
    numberChange(){
      if(!this.editAll[this.editField])
        return this.editAll[this.editField] = 0;
    },
    cancelEdit() { },
    openEdit(item) {
      this.editedIndex = this.dataTable.indexOf(item);
      this.editedItem = Object.assign({}, item);
    },
    closeEdit() {
      this.editedIndex = -1;
      this.editedItem = {};
    },
    onClickRefresh(){
      this.getAllDevices();
      this.$toasted.show(`successfully refresh`, { type: 'success' });
    },
    check(e, items) {
      if(!e) this.selected = [...this.selected].filter(point=>point.network_uuid != items[0].network_uuid);
      else this.selected = [...this.selected].concat(items);
    },
  },
};
</script>
<style scoped>
.centered-input >>> input {
  text-align: center;
}
.cursor-pointer {
  cursor: pointer;
  color: #1867c0;
}
.background-fault-true {
  background-color: #fbb93e;
}
.background-enable-false {
  background-color: #666666;
}
.breadcrumbs{
  margin-left: 0 !important;
  color: white;
  width: 800px;
}
.breadcrumbs-item-text {
  max-width: 200px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.table-styling {
  margin-right: 5px;
  margin-left: -5px;
}
.value{
  font-weight: bold;
  opacity: 0.6;
}
.overflow-td{
  overflow-y: auto;
  width: 150px;
  height: 65px;
  word-break: break-word;
}
</style>
