<template>
  <v-navigation-drawer absolute permanent  right app class="mb-right-sidebar float-left text-left">
    <v-list nav dense>
      <v-list-group no-action :value="true" :active-class="currentUrl == items[0].link ? 'background': ''" mandatory>
        <template v-slot:activator >
          <v-list-item-content >
            <v-list-item-title>
              <router-link :to="items[0].link" class="text-style"> {{items[0].name}} </router-link>
            </v-list-item-title>
          </v-list-item-content>
        </template>
        <v-list-group no-action sub-group v-for="(network, ni) in items[0].children" :key="ni" :value="true" :active-class="currentUrl == network.link ? 'background': ''" mandatory>
          <template v-slot:activator>
            <v-list-item-content>
              <v-list-item-title>
                <router-link :to="network.link" class="text-style"> {{network.name}} </router-link>
              </v-list-item-title>
            </v-list-item-content>
          </template>
          <v-list-item v-for="(device, di) in network.children" :key="di"  :to="device.link">
            <v-list-item-title >{{device.name}}</v-list-item-title>
          </v-list-item>
        </v-list-group>
      </v-list-group>
    </v-list>
  </v-navigation-drawer>
</template>

<script>
  export default {
    data: () => ({
      currentUrl: "",
    }),
    computed: {
      items () {
        return [
          { id: 1, name: 'All Networks', children: this.networksData, type:'modbus-master', link:"/modbus/networks" },
        ]
      },
      networksData () {
        const { mbNetworks } = this.$store.state;
        return this.getTreeViewData(mbNetworks);
      },
    },
    watch: {
      '$route.path': {
        handler: function(path) {
          this.currentUrl = path;
        },
        deep: true,
        immediate: true
      },

    },
    mounted() {
    },
    methods: {
      getTreeViewData(networksData){
        if(!networksData || networksData && networksData.length == 0){
          return;
        }
        const result = networksData.map(nw=>{
          if(nw.devices && nw.devices.length > 0){
            const devices = [];
            nw.devices.forEach(de => {
              const device = { id: de.uuid, name: `Device: ${de.name}`, type: 'device', link: `/modbus/devices/${de.uuid}/points` };
              devices.push(device);
            });
            return { id: nw.uuid, name: `Network: ${nw.name}`, children: devices, type: 'network', link: `/modbus/networks/${nw.uuid}/devices` };
          }
          else{
            return { id: nw.uuid, name: `Network: ${nw.name}`, type: 'network', link: `/modbus/networks/${nw.uuid}/devices` };
          }
        });
        return result;
      },

    },
  };
</script>
<style >
  .mb-right-sidebar .text-style{
    text-decoration: unset;
    color: white !important;
  }
  .mb-right-sidebar .v-list .v-list-item--active, .v-list .v-list-item--active .v-icon{
    color: white !important;
  }
  .background {
    background-color: #666666;
  }
</style>
